@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Roboto;
    background: #c2c1c2;
    color: #070707;
    min-height: 100vh;
}

.wrapper {
    padding: 2%;
    margin: 14px;
    margin-top: 50px;
    margin-bottom: 100px;
    transition: 0.5s;
    background-color: #efefef;
    height: 100%;
    bottom: 0;
    min-height: 100vh;
    position: relative;
}

.text-link, .text-link:focus, .text-link:hover, .text-link:visited, .text-link:link, .text-link:active {
    color: inherit;
    text-decoration: inherit;
}

.sidenav{
    color: #efefef;
    background: #022B3A;
    height: 100%;
    width: 200px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    overflow-x: hidden;
    padding-top: 10px;
    display:block;

    transition: 0.3s;
}

.navitem {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    display: block;
    border: 10px;
}

.navitems {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 20px;

    transition: 0.3s;
}

.navitem:hover {
    background-color: rgba(255,255,255,0.1);
    transition: 0.5s;
}

.sidenav-foot {
    bottom: 0;
    position: absolute;
    
    transition: 0.3s;
}

.sidenav-foot h3:hover {
    font-size: 3em;
    
    transition: 0.5s;;
}

.sidenav-foot h3:not(:hover) {
    
    transition: 0.5s;
}

.collapse-btn {
    background-color: #022B3A;
    border-radius: 0;
    border-bottom-right-radius: 40%;
    position: fixed;
    z-index: 0;
    left: 0;
    top: 0;
    transition: 0s;
    margin-left: 200px;

    transition: 0.3s;
}

.collapsed.sidenav {
    width: 0;
}


.collapsed.collapse-btn {
    margin-left: 0;
}

.collapsed {
    display:block;
}

.footer {
    background-color: #c2c1c2;
    bottom: 0;
    left: 0;
    width: 100%;
    position: relative;
    text-align: center; 

    font-weight: 800;
    color: #022B3A;

    justify-content: space-around;
    align-content: space-around;

    display: flex;
    flex-direction: row;

    padding-right: 10px;
}

.footer-icon {
    margin-bottom: 20px;
    justify-content: space-between;

    display: block;
    margin-left: auto;
    margin-right: auto;

    flex-direction: column;
}
img {
    max-width: 100%;
    width: 500px;
}

.post-container {
    margin-bottom: 20px;
}

.post-content {
    max-width: 100%;
    margin: 30px;
}

.post-header {
    margin-left: 10px;
}


